import React from "react"
import { HIs } from "@macashipo/mlib"
import useConfigTableCell from "../../hooks/useConfigTableCell"
import styled from "styled-components"

const WrapTreeNode = styled.div`
  display: inline-block;
  position: relative;
  & > div {
    display: block;
    list-style: none;
    width: 5px;
    height: 50%;
    margin-right: 4px;
    margin-left: 1px;
    &.three-node-v {
      border-left: 1px #a5a9ad dotted;
    }
    &.three-node-h {
      border-top: 1px #a5a9ad dotted;
    }
    &.three-node-h:after,
    &.three-node-v:after {
      content: ".";
      opacity: 0;
    }
    &.three-node-e {
      position: absolute;
      top: calc(50% - 3px);
      left: -2px;
      width: 8px;
      height: 8px;
      background: white;
      margin: 0px;
      border: 1px solid #0091c7;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 11px;
      line-height: 8px;
    }
  }
`
export default function MTCReadonly(props) {
  const ctc = useConfigTableCell({ props })
  const { cell, row } = props
  if (HIs.isObject(cell)) {
    return <div></div>
  }
  let _treeLevel = 0
  // let _lastOfLevel = false
  let _hasChild = false
  let _levelConfig = []
  if (ctc.more) {
    if (ctc.more["fLevel"] && row[ctc.more["fLevel"]]) {
      _treeLevel = row[ctc.more["fLevel"]]
    }
    // if (ctc.more["fLastOfLevel"] && row[ctc.more["fLastOfLevel"]]) {
    //   _lastOfLevel = true
    // }
    if (ctc.more["fHasChild"] && row[ctc.more["fHasChild"]]) {
      _hasChild = true
    }
    if (ctc.more["fLevelConfig"] && row[ctc.more["fLevelConfig"]]) {
      _levelConfig = row[ctc.more["fLevelConfig"]]
    }
  }
  // console.warn("props:", _treeLevel, ctc.more)
  // console.warn("_levelConfig:", row.Key, _levelConfig)
  return (
    <div className="mct-readonly_with_tree disabled" style={ctc.styleWrap()}>
      {_treeLevel >= 0 && (
        <div>
          {_levelConfig &&
            _levelConfig.map((v, i) => {
              let _class1 = ""
              let _class2 = ""
              if (v == 0) {
                _class1 = "three-node-v"
                _class2 = "three-node-v"
              } else if (v == 1) {
                _class1 = "three-node-v"
                _class2 = "three-node-v three-node-h"
              } else if (v == 2) {
                _class1 = "three-node-v"
                _class2 = "three-node-h"
              }
              return (
                <WrapTreeNode key={i} className="three-node">
                  <div className={_class1}> </div>
                  <div className={_class2}> </div>
                  {(v == 1 || v == 2) && _hasChild && (
                    <div className="three-node-e"> - </div>
                  )}
                </WrapTreeNode>
              )
            })}
          {/* {new Array(_treeLevel + 1).fill("").map((v, i) => {
            let _class1 = "three-node-v"
            let _class2 = "three-node-h"
            if (i < _treeLevel) {
              _class1 = "three-node-v"
              _class2 = "three-node-v"
              if (_treeLevel == 0) {
                _class1 = "three-node-v"
                _class2 = "three-node-v three-node-h"
              }
            } else if (i == _treeLevel) {
              _class1 = "three-node-v"
              _class2 = "three-node-v three-node-h"
              if (_lastOfLevel && _hasChild == false) {
                _class1 = "three-node-v"
                _class2 = "three-node-h"
              }
            }
            return (
              <WrapTreeNode key={i} className="three-node">
                <div className={_class1}> </div>
                <div className={_class2}> </div>
              </WrapTreeNode>
            )
          })} */}
        </div>
      )}
      {cell}
    </div>
  )
}
